.breadcrumb-component {
  color: #49afcd;
  cursor: default;
  font-size: 12px;
  font-weight: 300;
  height: 10px;
  line-height: 10px; }
  .breadcrumb-component-link {
    cursor: pointer; }
    .breadcrumb-component-link:hover, .breadcrumb-component-link:active {
      color: #0492ba;
      text-decoration: underline; }
  .breadcrumb-component.disabled {
    color: #ababab;
    pointer-events: none; }
